<template>
<div :style="image" class="image">
    <div class="middle">
    <v-container class="altura" >
        <v-card :style="image2">
            <v-card-title primary-title>
            <h4 class="centro branco">Register</h4>
            </v-card-title>
            <v-form>
            <v-text-field  dark name="Username" label="Username"></v-text-field>
            <v-text-field dark name="Password" v-model="password" label="Password" type="password"></v-text-field>
            <v-text-field dark name="Email" label="Email" v-model="email" type="email"></v-text-field>
            <v-text-field dark name="Mobile" label="Mobile" type="Mobile"></v-text-field>

            <v-card-actions>
            <v-btn primary large block @click="login()">Login</v-btn>
            </v-card-actions>

            <v-card-actions>
            <v-btn primary large block>Register</v-btn>
            </v-card-actions>

            </v-form>
        </v-card>
    </v-container>
    </div>
</div>


</template>

<script>

import firebase from "firebase";

 export default {
        data() {
            return {
               image: {backgroundImage: "url('https://www.pixelstalk.net/wp-content/uploads/2016/07/Beer-Desktop-Background.jpg'"},
                email: '',
                password: '',
                image2: {backgroundImage: "url('https://www.pixelstalk.net/wp-content/uploads/2016/07/Beer-Desktop-Background.jpg'"},
               

            };
        },
        
        methods: {
            login(){
                firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
                    .then((userCredential) => {
                        // Signed in 
                        var user = userCredential.user;
                        console.log(user);
                        // ...
                    })
                    .catch((error) => {
                        var errorCode = error.code;
                        var errorMessage = error.message;
                        console.log(errorCode +errorMessage );
                        // ..
                    });
            }

        }
    };



</script>

<style>

    .branco{
     color: white;
    }

    .middle{
        display: flex;
        justify-content: center;
        height: 100%;
        padding-top: 50px;
    }

    .centro{
        justify-content: center;
        padding-left: 190px;
    }

    .altura{
        width: 500px;

    }

    .image {
        height: 900px;
        background-repeat: no-repeat;
        background-size: cover;
    }
    
</style>
